.collection-item {
    width: 22vw;
    display: flex;
    flex-direction: column;
    position:relative;
    height: 350px;
    align-items: center;
  
    .image {
      width: 100%;
      height: 95%;
      background-size: cover;
      background-position: center;
      margin-bottom: 5px;
    }
    
    .custom-button{
      width: 60%;
      opacity: 0.7;
      position:absolute;
      top: 65%;
      display:none;
    }
    &:hover{
      .image{
        opacity: 0.8;
      
      }
      .custom-button{
        opacity: 0.85;
        display: flex;
      }
    }

    .collection-footer {
      width: 100%;
      height: 5%;
      display: flex;
      justify-content: space-between;
      font-size: 18px;
  
      .name {
        width: 90%;
        margin-bottom: 15px;
      }
  
      .price {
        width: 10%;
      }
    }
  }